html,
body {
  font-family: "微软雅黑,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Helvetica,PingFang SC,Hiragino Sans GB,Microsoft YaHei,SimSun,sans-serif";
  color: #fff; }

div,
p,
h1,
h2,
h3,
h4,
h5,
main,
ul,
li,
ol,
dl,
dt,
dd,
section,
nav,
header,
footer,
aside,
a {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: #fff; }

ul,
li,
ol,
dl,
dd,
dt {
