img {
  max-width: 100%; }

/*
* tools
*/
header {
  position: fixed;
  width: 100%;
  z-index: 99;
  padding: .4rem 1rem; }
  header nav {
    float: right; }
    header nav a {
      font-size: .28rem;
      font-weight: 400;
      margin: 0 0.5rem 0 0;
      position: relative;
      display: inline-block; }
      header nav a:after {
        content: " ";
        transition: all .5s;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background-color: transparent; }
      header nav a.active:after, header nav a:hover:after {
        transition: all .5s;
        background-color: #3ff8ff;
        bottom: -.15rem; }
      header nav a:last-of-type {
        margin-right: 0; }
    header nav #aboutus {
      padding: 0.08rem .25rem;
      border-radius: .8rem;
      border: 1px solid #f1f3f5; }
      header nav #aboutus:after {
        display: none; }
  header #personinfo {
    display: none;
    position: absolute;
    width: 140px;
    right: 0;
    top: 0;
    height: 100%; }
    header #personinfo #avatar {
      width: .8rem;
      height: .8rem;
      border-radius: 50%;
      margin-top: 0.3rem;
      margin-left: 0.2rem; }
    header #personinfo #username {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      font-size: .28rem;
      margin-left: .2rem; }
    header #personinfo ul {
      position: absolute;
      width: 100%;
      padding-left: 0.2rem;
      background: rgba(0, 0, 0, 0.1);
      top: 100%;
      left: 0;
      overflow: hidden;
      height: 0;
      transition: all .5s; }
      header #personinfo ul li {
        font-size: .3rem;
        line-height: 1.2rem; }
    header #personinfo:hover ul {
      height: 1.2rem; }
  header.haslogined {
    padding-right: 140px; }
    header.haslogined #login,
    header.haslogined #reg {
      display: none; }
    header.haslogined #personinfo {
      display: block; }

@media (max-width: 960px) {
  header {
    padding: .4rem .8rem; }
    header nav a {
      margin: 0 0.28rem 0 0; } }

@media (max-width: 768px) {
  header {
    padding: .2rem .4rem; }
    header img {
      width: 1.8rem; }
    header nav a {
      margin: 0.07rem 0.2rem 0 0; }
      header nav a:nth-of-type(-n+5) {
        display: none; }
    header nav #aboutus {
      padding: 0.07rem .15rem; }
    header #personinfo #avatar {
      width: .8rem;
      height: .8rem;
      border-radius: 50%;
      margin-top: 0.1rem;
      margin-left: 0.2rem; } }

@media (max-width: 480px) {
  header {
    background: rgba(0, 0, 0, 0.1); }
    header.haslogined {
      padding-right: 90px; }
    header #personinfo {
      width: 90px; }
    header img {
      margin-top: 0.05rem; } }

.banner {
  height: 210px;
  background: #152d82 url("/assets/images/news/newsbg.jpg") no-repeat 50% -70px;
  padding: 0 1.2rem;
  position: relative; }
  .banner h2 {
    margin: 0;
    height: 210px;
    color: #f5f5f5;
    font-size: .6rem;
    font-weight: 100; }
    .banner h2 div {
      position: absolute;
      top: 50%;
      transform: translateY(-50%); }
      .banner h2 div img {
        margin-top: 8px; }

.banner.staff {
  background: #152d82 url("/assets/images/aboutus/staff-bnr.jpg") no-repeat 50% -70px; }

.banner.aboutus {
  background: #152d82 url("/assets/images/aboutus/about-bnr.jpg") no-repeat 50% -70px; }

.banner.joinus {
  background: #152d82 url("/assets/images/aboutus/joinus-bnr.jpg") no-repeat 50% -70px; }

@media (max-width: 768px) {
  .banner {
    height: 180px; }
    .banner h2 {
      height: 180px; } }

@media (max-width: 480px) {
  .banner,
  .banner.staff,
  .banner.aboutus,
  .banner.joinus {
    height: 90px;
    background-position: 50% -10px;
    background-size: cover; }
    .banner h2,
    .banner.staff h2,
    .banner.aboutus h2,
