footer {
  background: #24293e; }
  footer .inner {
    padding: .8rem 0;
    overflow: hidden; }
    footer .inner ul {
      float: left;
      width: 60%; }
      footer .inner ul li {
        float: left;
        margin-right: 1.4rem;
        padding-top: 1rem; }
        footer .inner ul li a,
        footer .inner ul li p {
          display: block;
          font-size: .3rem;
          line-height: .5rem;
          letter-spacing: 1px;
          transition: color .5s; }
        footer .inner ul li a:hover {
          color: #2caaeb; }
        footer .inner ul li:last-of-type {
          margin-right: 0; }
    footer .inner .imgbox {
      float: right;
      width: 40%; }
      footer .inner .imgbox img {
        width: 40%; }
        footer .inner .imgbox img:first-of-type {
          margin-right: 10%; }
  footer .copyright {
    padding: 0.2rem 0;
    background-color: #1b1f33;
    color: #5a5e6d;
    text-align: center; }
    footer .copyright a {
      color: #5a5e6d;
      transition: color .5s; }
      footer .copyright a:hover {
        color: #2caaeb; }

@media (max-width: 992px) {
  footer .inner {
    padding: 0 20px; }
    footer .inner ul {
      width: 100%; }
      footer .inner ul li {
        padding-top: .2rem;
        padding-bottom: .2rem; }
    footer .inner .imgbox {
      float: none;
