      footer .inner .imgbox img {
        width: initial;
        max-width: 100%; } }

@media (max-width: 640px) {
  footer .inner ul {
    display: none; }
    footer .inner ul li {
      margin-right: .5rem; }
  footer .inner .imgbox {
    text-align: center;
    padding-top: 15px; }
    footer .inner .imgbox img {
      float: none;
