.container {
  max-width: 1000px;
  margin: 0 auto; }

header {
  background-color: #122466;
  position: relative; }

#news-list,
#staff-list {
  margin-bottom: 30px;
  *zoom: 1; }
  #news-list:after,
  #staff-list:after {
    content: " ";
    clear: both;
    display: table; }
  #news-list li,
  #staff-list li {
    float: left;
    width: 30%;
    margin: .8rem 5% 0 0;
    padding: 0.35rem;
    border: 1px solid transparent;
    transition: all .5s; }
    #news-list li .imgbox div,
    #staff-list li .imgbox div {
      overflow: hidden; }
    #news-list li img,
    #staff-list li img {
      width: 100%; }
    #news-list li h3,
    #staff-list li h3 {
      font-size: .3rem;
      line-height: .38rem;
      height: 0.76rem;
      margin: .3rem 0;
      color: #222;
      font-weight: 400;
      overflow: hidden;
      display: -webkit-box;
      word-break: break-all;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical; }
    #news-list li p,
    #staff-list li p {
      color: #0a1026;
      font-size: .25rem;
      line-height: .4rem;
      margin-bottom: .2rem; }
    #news-list li span,
